import Vue from 'vue'
import Vuex from 'vuex'
/* 下载本地存储 cnpm install vuex-persistedstate  --save */
// 引入自动存储的插件
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex)


export default new Vuex.Store({
    plugins: [createPersistedState()],//自动存储
    state:{
        log:"",     //id
        photo:'',   //换头像

        /*  详情页   */
        Image:[],       //换皮肤
        towshop:[],     //购物车
        price:'',       //单价
        sum:'',         //总价
        subAll:''       //总数量
    },
    getters:{},
    mutations:{
        elm(state,v){
            state.log=v
            console.log(state.log.id)
        },
        //退出登录
        del(state){
            state.log=''
        },
        photo(state,val){
            state.photo=val
        },
        //详情页皮肤图片
        skin(state,Img){
            state.Image=Img
            //console.log(state.Image)
        },
        //购物车
        towshop(state,zhi){
            state.towshop=zhi
            // console.log(state.towshop)
        },
        //购物车单价
        price(state,numBer){
            state.price=numBer
        },
        //总价
        sum(state,numberAll){
            state.sum=numberAll
        },
        subAll(state,sub){
            state.subAll=sub
        }
    },
    actions:{},
})


