import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)



const Meituan = () => import("@/components/meituan");
const Cityone = () => import("../page/city/city_one");
const SeekCity = () => import("../page/city/seek_city");
const Message =() => import("@/page/logon/message");
const Logon =()=> import("@/page/logon/logon")
const Content =()=> import("../components/content");
const My_message = () => import('@/page/account/my_message')
// const Seek = () => import('@/page/account/seek')
const Order = () => import('@/page/order/order');
const Site = () => import('@/page/account/site');
const Address = () => import('@/page/Address/address');
const addDetail = () => import('@/page/Address/addDetail');
const shopDetails = ()=> import('@/page/shop/shopDetails');
const WatchComputed = ()=> import('@/page/watch_computed/watch_computed');   //watch/computed


export default new Router({
  routes: [
    {
      path: '/',
      name: 'meituan',
      component: Meituan,
      redirect:'/content',
      children:[
        {
          path: '/content',
          name: 'Content',
          component: Content,
        },
        {
          path: '/message',       //我的登录信息
          name: 'Message',
          component: Message,
        },
        
      ]
    },
    {
      path: '/Cityone',           //热门城市
      name: 'Cityone',
      component:Cityone,
    },
    {
      path: '/seekcity/:geohash', //搜索城市
      name: 'SeekCity',
      component:SeekCity,
    },
    {
      path:'/logon',
      name:'Logon',
      component:Logon
    },
    // {
    //   path: '/seek',         //底部搜索页面
    //   name: 'Seek',
    //   component:Seek,
    // },
    {
      path: '/order',        //订单
      name: 'Order',
      component:Order,
    },
    {
      path: '/my_message',    //账户信息
      name: 'My_message',
      component: My_message,
    },
    {
      path: '/site',
      name: 'Site',
      component: Site,
    },
    {
      path: '/address',       //新增地址
      name: 'Address',        
      component: Address,
      meta:{keepAlive:true}   //缓存这个组件
    },
    {
      path: '/addDetail',     //搜索地址
      name: 'addDetail',
      component: addDetail,
    },
    {
      path: '/shopDetails',   //购物详情页
      name: 'shopDetails',
      component: shopDetails,
    },
    {                         //watch监听和computed计算属性练习
      path:'/watchcomputed',
      name:'WatchComputed',
      component:WatchComputed
    }
  ]

})




