// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
Vue.use(router)

Vue.config.productionTip = false

import axios from 'axios'
Vue.prototype.axios=axios

//vuex
// import store from './Vuex/index'
import store from './Vuex/home'

// element-Ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI)
//vant  图标
import { Icon } from 'vant';
Vue.use(Icon);
// vant 开关
import 'vant/lib/button/style';
import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);
// vant 弹出层
import { Popup } from 'vant';
Vue.use(Popup);
// vant 单元格布局
import { Cell, CellGroup } from 'vant';
Vue.use(Cell).use(CellGroup);
// vant 按钮
import { Button } from 'vant';
Vue.use(Button);
// vant 侧边栏导航
import { Sidebar, SidebarItem } from 'vant';
Vue.use(Sidebar);
Vue.use(SidebarItem);

/*路由拦截main.js中
  1.->引入vue和vue-router还有，创建一个route对象
    import Vue from 'vue'
    import router from './router'
    Vue.use(router)
    在这里下载的时候vue对象已经自动创建配置好了直接写第二步
  2.->router对象在路由进入之前beforeEach((to.from,next)=>{})
  to 要进入的   from    next默认要进入的路由
  这里已经引入了vuex仓库，所以判断的时候store前不需要$
*/

router.beforeEach((to,from,next)=>{
  //console.log(to)
  if(to.path=='/logon'){
    next();
  }else{
    ('您还没有登录，请先登录')
    if(store.state.log.id==false){
      next('/logon')
    }else{
      next()
    }
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})




